@font-face {
  font-family: Roboto-Regular;
  src: url("Roboto-Regular.33edf6e3.ttf");
}

@font-face {
  font-family: Roboto-Bold;
  src: url("Roboto-Bold.3085ad67.ttf");
}

* {
  box-sizing: border-box;
  color: #242742;
}

body {
  background-color: #242742;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-family: Roboto-Regular;
  display: flex;
}

h1 {
  font-family: Roboto-Bold;
  font-size: 3.5rem;
  font-weight: 300;
}

.card {
  background-color: #fff;
  border-radius: 20px;
  gap: 10px;
  width: 800px;
  padding: 30px;
  display: flex;
}

.left {
  width: 60%;
}

.right {
  width: 40%;
}

.right img {
  width: 100%;
}

ul {
  margin-top: 5%;
  margin-bottom: 10%;
  padding-left: 30px;
  list-style-image: url("icon-list.a05c503c.svg");
}

li {
  margin-bottom: 10px;
}

.email-label, label {
  font-family: Roboto-Bold;
  font-size: .8em;
}

form {
  flex-direction: column;
  width: 300px;
  font-family: inherit;
  display: flex;
}

label {
  margin-bottom: 10px;
}

input {
  border: 1px solid #36384e;
  border-radius: 5px;
  outline: none;
  padding: 20px;
  font-family: inherit;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #242742;
  border: none;
  border-radius: 5px;
  outline: none;
  margin-top: 20px;
  padding: 15px 20px;
  font-family: Roboto-Bold;
  font-size: .9rem;
}

button:hover {
  color: #fff;
  background-color: #ff6257;
  transition-duration: .7s;
}

.hide {
  display: none;
}

.card-2 {
  background-color: #fff;
  border-radius: 40px;
  width: 480px;
  padding: 50px;
}

.card-2 h2 {
  margin-top: 20px;
  font-family: Roboto-Bold;
  font-size: 3rem;
  font-weight: 300;
}

.icon-card-2 {
  width: 60px;
}

.card-2 button {
  width: 100%;
}

.card-2 p {
  line-height: 1.6em;
}

@media (width <= 768px) {
  .card {
    flex-direction: column-reverse;
    height: 100vh;
  }

  .card h1 {
    font-size: 2.5em;
  }

  .left {
    width: 100%;
    padding: 20px;
  }

  .right {
    width: 100%;
  }

  .right img {
    content: url("illustration-sign-up-mobile.3ae32ac4.svg");
  }

  body {
    padding: 0;
  }

  form {
    width: 100%;
  }

  .card-2 {
    min-height: 100vh;
    padding-top: 20vh;
  }
}

